import Page from 'components/Page';
import React from 'react';
import { Button,CardSubtitle,
 Card, CardBody, Col, Row, Table,DropdownItem, DropdownMenu, DropdownToggle, UncontrolledButtonDropdown} from 'reactstrap';


const tableTypes = ['striped'];
class ButtonPage extends React.Component {
  state = {
    rSelected: null,
    cSelected: [],
    aSelected: "Chn i tng",
  };
  
  onCheckboxBtnClick(selected) {
    const index = this.state.cSelected.indexOf(selected);
    if (index < 0) {
      this.state.cSelected.push(selected);
    } else {
      this.state.cSelected.splice(index, 1);
    }
    this.setState({ cSelected: [...this.state.cSelected] });
  }
  

  render() {
    return (
      <Page
        title="Phn quyn"
        breadcrumbs={[{ name: 'Phn quyn', active: true }]}
        className="TablePage"
      >
        {tableTypes.map((tableType, index) => (
          <Row key={index}>
            <Col>
              <Card className="mb-3">
                <CardBody>
                <Row>
                    <Col>
                        <UncontrolledButtonDropdown>
                          <DropdownToggle caret>{this.state.aSelected}</DropdownToggle>
                          <DropdownMenu >
                            <DropdownItem onClick={() => this.setState({ aSelected: "Ph trch CLB" })}
                                          active={this.state.aSelected === "Ph trch CLB"}>Ph trch CLB</DropdownItem>
                            <DropdownItem onClick={() => this.setState({ aSelected: "Cng tc vin vit bi" })}
                                          active={this.state.rSelected === "Cng tc vin vit bi"}>Cng tc vin vit bi</DropdownItem>
                            <DropdownItem onClick={() => this.setState({ aSelected: "Cng tc vin k ton" })}
                                          active={this.state.rSelected === "Cng tc vin k ton"}>Cng tc vin k ton</DropdownItem>
                          </DropdownMenu>
                        </UncontrolledButtonDropdown>
                    </Col>
                  </Row>
                <Table {...{ [tableType || 'default']: true }}>
                <Row>
                  <Col md={8}>
                    <Card>
                      <CardBody>
                      <Table {...{ [tableType || 'default']: true }}>
                      <thead></thead>
                      <tbody>
                        

                        <tr>
                          <td>
                        <CardSubtitle >Phn quyn</CardSubtitle> 
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(1)}
                            active={this.state.cSelected.includes(1)}
                            > </Button> Phn quyn<br/>
                          </td>
                        </tr>

                        <tr>
                          <td>
                          <CardSubtitle >Qun tr ti khon</CardSubtitle>                         
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(2)}
                            active={this.state.cSelected.includes(2)}
                            > </Button> Nhm ngi dng<br/>
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(3)}
                            active={this.state.cSelected.includes(3)}
                            > </Button> Ngi dng <br/>
                          </td>
                        </tr>

                        <tr>
                          <td>
                          <CardSubtitle >Qun tr hot ng thin nguyn</CardSubtitle>                          
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(4)}
                            active={this.state.cSelected.includes(4)}
                            > </Button> Danh sch hot ng<br/>

                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(5)}
                            active={this.state.cSelected.includes(5)}
                            > </Button> Tin hot ng<br/>
                          </td>
                        </tr>

                        <tr>
                          <td>
                          <CardSubtitle >Qun tr hot ng ti tr</CardSubtitle>                         
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(6)}
                            active={this.state.cSelected.includes(6)}
                            > </Button> Danh sch kho st<br/>                          
                          </td>
                        </tr>
                        

                        <tr>
                          <td>
                          <CardSubtitle >Qun tr lung quyn gp</CardSubtitle>                          
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(7)}
                            active={this.state.cSelected.includes(7)}
                            > </Button> Np tin<br/>                                                  
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(8)}
                            active={this.state.cSelected.includes(8)}
                            > </Button> Chuyn tin<br/>
                          </td>
                        </tr>

                        <tr>
                          <td>
                          <CardSubtitle >Qun tr gp </CardSubtitle>                          
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(9)}
                            active={this.state.cSelected.includes(9)}
                            > </Button> Qun tr gp <br/>                          
                          </td>
                        </tr>

                        <tr>
                          <td>
                          <CardSubtitle >Bo co</CardSubtitle>                          
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(10)}
                            active={this.state.cSelected.includes(10)}
                            > </Button> Np tin<br/>
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(11)}
                            active={this.state.cSelected.includes(11)}
                            > </Button> Kho st<br/>
                          <Button
                            color="primary"
                            onClick={() => this.onCheckboxBtnClick(12)}
                            active={this.state.cSelected.includes(12)}
                            > </Button> Quyn gp<br/>
                            </td>
                          </tr>
                          
                        </tbody>
                      </Table>
                    </CardBody>
                  </Card>
                </Col>

                </Row>
                </Table>                
               </CardBody>
              </Card>
            </Col>
          </Row>
        ))}
      </Page>
    );
  };
}

export default ButtonPage;
